.navbar {
  width: 100%;
  height: auto;
  background: #349499;
  position: fixed;
  top: 0;
  z-index: 99;
}
.navbar .scroll__status {
  height: 2px;
  width: 0%;
  background-color: #E4C1AE;
  transition: 0.25s ease;
  top: 0;
  left: 0;
}
.navbar .navbar__container {
  max-width: 1440px;
  margin: 0 auto;
}
.navbar .navbar__container .logo {
  padding: 0.75rem;
  color: #E4C1AE;
  font-size: 1.25rem;
}